body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
#root,
.App {
  height: 100%;
  width: 100%;
  background-color: rgb(84, 20, 44);
  font-family: 'Times New Roman', serif;
  height: 100%;
  background: url(/lpr-website/website-front-banner.jpg);
  background-attachment: fixed;
  background-size: cover;
  object-fit: cover;
  background-position: 50% top;
}

.navbar-brand {
  float: left;
  justify-content: left;
}

.home-page-content {
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
  margin: 50px auto;
  max-height: 95%;
  text-align: center;
  box-shadow: 1px 1px 25px rgb(0, 0, 0);
  width: -webkit-max-content;
  width: max-content;
}

.col {
  margin-top: 200px;
}

.main-content-wrapper {
  border-radius: 20px;
  padding-top: 30px;
  padding-bottom: 50px;
  margin: auto;
  max-width: 80%;
  position: relative;
}

.main-content-wrapper img {
  max-width:100%;
  max-height:100%;
}

.main-content-paragraph-wrapper {
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 5px;
  padding: 10px;
  margin: 20px auto;
  max-width: 95%;
  max-height: 95%;
  position: relative;
  color: rgb(255, 255, 255);
  text-align: left;
  font-size: calc(12px + 0.5vw);
  box-shadow: 1px 1px 10px black;
}

.centered-text-paragraph {
  text-align: center;
}

img.resize {
  height: auto;
}

iframe {
  width: 80%;
}

ul {
  list-style-position: inside;
}

#about-us-button {
  background-color: rgba(128, 0, 0, 0.8);
  color: white;
  border-color: white;
}

.main-content-wrapper h1 > strong {
  text-shadow: 2px 2px 5px black;
}

#ig-profile {
  padding: 10px;
  margin: auto;
  width: -webkit-max-content;
  width: max-content
}
.App > nav {
    padding-bottom: 0;
    padding-top: 0;
    background-color: rgb(0, 0, 0, 0) !important;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index:99;
}

/* .navbar {
    color: rgb(192, 0, 0) !important;
} */

.App > nav > div.container {
    margin: 0;
    max-width: 100%;
}

/* title */
.App > nav > div > a {
    font-size: 30px;
    color: white;
    text-shadow: 2px 2px 5px black;
}

.App > nav > div > a:hover {
    font-size: 30px;
    color: maroon;
}


/*nav links*/
#basic-nav-dropdown{
    font-size: 18px;
    text-shadow: 2px 2px 5px black;
    color: white;
}

#basic-nav-dropdown:hover{
    color: rgb(0, 0, 0);
}
#brothers-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.founding-chapter-list {
  width: 30%;
}

.founding-chapter-titles {
  font-size: calc(20px + 0.5vw);
  font-weight: bold;
}

@media (max-width: 1200px) {
  .founding-chapter-list {
    width: 50%;
  }
}
@media (max-width: 700px) {
  .founding-chapter-list {
    width: 100%;
  }
}

#alumni-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.alumni-chapter-list {
  width: 100%;
}

